.content{
    .catogary{
        ul{
            max-width: 1386px;
            box-sizing: border-box;
            margin: 0 auto;
        }
        li{ 
            width: 100%;
            height: 103px;
            box-sizing: border-box;
            border:1px solid #DCDCDC;
            text-align: center;
            font-weight: bolder;
            @include pad(){
                height: 70px;
            }
            @include phone(){
                height: 35px;
            }
            a{ 
                display: block;
                text-decoration: none;
                color: #000;
                font-size: 42px;
                line-height: 63px;
                width: 100%;
                padding: 20px 0 ;
                @include pad(){
                    padding: 14px 0;
                    font-size: 28px;
                    line-height: 42px;
                }
                @include phone(){
                    padding: 8px 0;
                    font-size: 14px;
                    line-height: 19px;
                }
            }
            &:hover{
                border-bottom: 16px solid $primary;
                transition: all 0.5s;
                @include pad(){
                    border-bottom: 8px solid $primary;
                }
                @include phone(){
                    border-bottom: 4px solid $primary;
                }
            }
        }
        .active{
            border-bottom: 16px solid $primary;
            @include pad(){
                border-bottom: 8px solid $primary;
            }
            @include phone(){
                border-bottom: 4px solid $primary;
            }
        }
        
    }
    .diamond{
        img{
            vertical-align: text-top;
            margin-right: 8px;
            @include pad(){
                width: 32px;
            }
            @include phone(){
                width: 15px;
            }
        }
    }
    .sun{
        img{
            vertical-align: text-top;
            margin-right: 5px;
            width: 45px;
            @include pad(){
                width: 32px;
            }
            @include phone(){
                width: 14px;
            }
        }
    }
    .thunder{
        img{
            vertical-align: sub;
            margin-right: 8px;
            @include pad(){
                width: 23px;
            }
            @include phone(){
                width: 10px;
            }
        }
    }
    .icon{
        display: inline-block;
    }
    .banner-left{
        margin-right: 15px;
        height: 309px;
        width: 945px;
        @include pad(){
            margin: 0;
        }
        @include phone(){
            height: 144px;
        }
    }
    .banner-right{
        margin-left: 15px;
        height: 309px;
        width: 945px;
        @include pad(){
            margin: 0;
        }
        @include phone(){
            height: 144px;
        }
    } 
    .optical-right{
        background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner-2.png?raw=true");
    }
    .optical-left{
        background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner-1.png?raw=true");
    }
    .products{
        text-align: center;
        padding-top: 88px;
        @include pad(){
            padding-top: 40px;
        }
        @include phone(){
            padding-top: 30px;
        }
        .container{
            max-width: 1110px;
            margin: 0 auto;
            h1{
                font-weight: bolder;
                font-size: 42px;
                line-height: 63px;
                @include phone(){
                    font-size: $base-font-size;
                    line-height: 26px;
                }
            }
            h2{
                font-weight: bolder;
                font-size: $font-size-xl;
                line-height: $line-height-xl;
                margin-bottom: 32px;
                @include phone(){
                    font-size: $base-font-size;
                    line-height: 22px;
                    margin-bottom: 20px;
                }
            }
            .products-list{
                width: 100%;
                justify-content: center;
                flex-wrap: wrap;
                box-sizing: border-box;
                @include pad(){
                    max-width: 690px;
                }
                @include phone(){
                    max-width: 345px;
                    padding: 0 15px;
                }
                .optical-card{
                    max-width: 23%;
                    margin: 0 1% 104px 1%;
                    @include pad(){
                        max-width:48%;
                        margin: 0 2% 104px 2%;
                    }
                    @include phone(){
                        max-width:100%;
                        margin: 0 0 32px 0;
                    }
                  
                    a{
                        overflow: hidden;
                        position: relative;
                        width: 100%;
                        display: block;
                        &::before{
                            content: "";
                            width: 100px;
                            height: 100px;
                            background: $primary;
                            position: absolute;
                            z-index: 1;
                            top: 20%;
                            left: 30%;
                            border-radius: 100%;
                            transform: scale(0);
                            transition-property: transform;
                            transition: 300ms ease-out;
                        }
                        &:hover::before{
                            opacity: 0.5;
                            transform: scale(3.5);
                        }
                    }
                    span{
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        display: inline-block;
                        width: 100%;
                        overflow: hidden;
                        line-height: 164px;
                        z-index: 2;
                        color: $white;
                        opacity: 0;
                        &:hover{
                            opacity: 1;
                            transition: all 1s;
                        }
                    }
                    img{
                        width: 100%;
                        height: 164px;
                        @include pad(){
                            display: none;
                        }
                    }
                    .d-pad{
                        max-width:330px;
                        height: 164px;
                        display: none;
                        @include pad(){
                            display:inline-block;
                        }
                        @include phone(){
                            max-width:345px;
                        }
                    }
                    
                }
                .card-text{
                    justify-content: space-between;
                    font-size: $font-size-l;
                    line-height: $line-height-l;
                    font-weight: bolder;
                    @include pad(){
                        line-height: $line-height-m;
                    }
                    @include phone(){
                        font-size: $base-font-size;
                        
                    }
                    .price{
                        color:$primary;
                    }
                }
                .color-list{
                    .color-box1{
                        width: 24px;
                        height: 24px;
                        border-radius: 4px;
                        background-color: #5F3E2D;
                        margin-right: 8px;
                    }
                    .color-box2{
                        width: 24px;
                        height: 24px;
                        border-radius: 4px;
                        background-color:#B75929;
                    }
                }
            }
            
        }    
    }
        
    .pagination{
        margin-bottom: 156px;
        @include pad(){
            margin-bottom: 104px;
        }
        @include phone(){
            margin-bottom: 56px;
            margin-top: 8px;
        }
        .container{
            max-width:100%;
            ul{
                justify-content: center;
                li{
                    border:1px solid #555555;
                    a{
                        text-decoration: none;
                        color:#555555;
                        width: 100%;
                        display: inline-block;
                        padding: 10px 15px;
                        @include phone(){
                            font-size: 12px;
                            line-height: 18px;
                            padding: 8px 8px;
                        }
                    }
                }
                .active{
                    background-color: #000000;
                    a{
                    color:#fff; 
                    }
                }
                .bg-gray{
                    background: #F2F2F2;
                }
            }
            .d-lg{
                display: none;
                @include pad(){
                    display: block;
                }
            }
        }
    }
}